html,
body {
  background: #202020;
  font-family: "Courier New", monospace !important;
}

._8pxborder {
  border-radius: 8px;
}

a,
a p {
  text-decoration: none;
}

.spreadOnHover {
  transition: letter-spacing 0.25s ease 0s;
}

.spreadOnHover:hover {
  letter-spacing: 4px;
}

.heart {
  display: none;
  height: 36px;
  width: 36px;
  position: absolute;
  bottom: 4px;
  left: 56px;
}

/* FIX BUGS from menu */
.page-wrap-closed {
  transform: translate3d(0px, 0px, 0px) !important;
}

.bm-menu-wrap-opened {
  display: block !important;
  transform: translate3d(0px, 0px, 0px) !important;
}

.bm-menu-wrap-closed {
  transform: translate3d(-100%, 0px, 0px) !important;
}

.bm-overlay-opened {
  opacity: 1 !important;
}

.bm-overlay-closed {
  opacity: 0 !important;
}

/* Position and sizing of burger button */
.bm-burger-button {
  z-index: 999 !important;
  background-color: #e0e0e0;
  height: 48px;
  width: 48px;
  border: 2px solid #202020;
  border-radius: 100%;
  top: 16px;
  right: 16px;
  left: auto;
  position: fixed;
}

/*sync the transition*/
.bm-menu-wrap {
  transition: all 0.25s ease 0s !important;
}

/*both of these will follow the menu when we add a particular class*/
.bm-burger-button,
#topFixed {
  transition: all 0.25s ease 0s !important;
}

#page-wrap {
  transition: all 0.25s ease 0s !important;
}

.bm-overlay {
  transition: opacity 0.25s ease 0s !important;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  height: 4px !important;
  background: #202020;
  width: 24px !important;
  margin: auto;
  position: absolute !important;
  padding: 0px !important;
  top: 0px;
  bottom: auto !important;
}

/*what makes the menu icon*/
.bm-burger-bars:nth-of-type(1) {
  top: 12px !important;
}

.bm-burger-bars:nth-of-type(2) {
  top: 20px !important;
}

.bm-burger-bars:nth-of-type(3) {
  top: 28px !important;
}

/* General sidebar styles */
.bm-menu {
  overflow: auto;
  overscroll-behavior: contain;
  background-color: #e0e0e0;
  color: #202020;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #e0e0e0;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.5) !important;
}

/* making the little X disapear */
.bm-cross-button {
  display: none !important;
}

.bm-overlay-opened:hover,
.clickable:hover {
  cursor: pointer;
}

/*
fixing prevent scroll issue (didn't work)
https://css-tricks.com/prevent-page-scrolling-when-a-modal-is-open/
*/
.other-opened {
  overflow-x: hidden !important;
  height: 100% !important;
}

.other-closed {
  height: auto !important;
  /*overflow-x: auto !important;*/
}

.aSection:hover {
  cursor: pointer;
}

.underlineLinks {
  text-decoration: underline !important;
}

.underlineOnHover:hover {
  text-decoration: underline !important;
}

/*for the play button that appears in front of GIFs*/
.ff-container .ff-overlay {
  max-width: 56px !important;
  max-height: 56px !important;
}
